#firstMainView {
	background-color:black;
	position:absolute;
	text-align:center;
	width:100vw;
	height:100vh;
	user-select:none;
}

#jet-plane {
	height:10vh;
	width:10vh;
	position:absolute;
	z-index:104;
	user-select:none;
}

.bug {
	height:10vh;
	width:10vh;
	z-index:101;
	background:none;
	position:absolute;
	user-select:none;
}

#blocker {
	background-color:grey;
	height:100%;
	width:100%;
	position:absolute;
	z-index:105;
	opacity:0.7;
}

#startButton {
	position:absolute;
	
	z-index:106;
	
	height:20vh;
	width:50vw;
	border:solid 1px black;
	border-radius:10vh;
	background-color:blue;
	font-size:12vh;
	font-family: 'Permanent Marker', cursive;
	box-shadow:0 10px 0 0 grey;
	outline:none;
}

#startButton:active {
	
	
	animation:none;
	
	border:solid 1px black;
	transform:translateY(4px);
	box-shadow:0 3px 0 0 grey;
}



@media only screen and (max-width:600px) {
	
	
	#startButton {
		position:absolute;
		
		z-index:106;
		
		height:20vh;
		width:50vw;
		border:solid 1px black;
		border-radius:10vh;
		background-color:blue;
		font-size:5vh;
		
		box-shadow:0 10px 0 0 grey;
		outline:none;
}
}


