#firstMainView {
	background-color:black;
	position:absolute;
	text-align:center;
	width:100vw;
	height:100vh;
	user-select:none;
}

#jet-plane {
	height:10vh;
	width:10vh;
	position:absolute;
	z-index:104;
	user-select:none;
}

.bug {
	height:10vh;
	width:10vh;
	z-index:101;
	background:none;
	position:absolute;
	user-select:none;
}

#blocker {
	background-color:grey;
	height:100%;
	width:100%;
	position:absolute;
	z-index:105;
	opacity:0.7;
}

#easyButton {
	
	
	z-index:107;
	
	height:20vh;
	width:35vw;
	border:solid 1px black;
	border-radius:10vh;
	background-color:blue;
	font-size:12vh;
	font-family: 'Permanent Marker', cursive;
	box-shadow:0 10px 0 0 grey;
	outline:none;
}

#easyButton:active {
	
	
	animation:none;
	
	border:solid 1px black;
	transform:translateY(4px);
	box-shadow:0 3px 0 0 grey;
	color:grey;
}

#mediumButton {
	
	box-sizing:border-box;
	z-index:107;
	padding:0;
	height:20vh;
	width:35vw;
	border:solid 1px black;
	border-radius:12vh;
	background-color:blue;
	font-size:12vh;
	font-family: 'Permanent Marker', cursive;
	box-shadow:0 10px 0 0 grey;
	outline:none;
}

#mediumButton:active {
	
	
	animation:none;
	
	border:solid 1px black;
	transform:translateY(4px);
	box-shadow:0 3px 0 0 grey;
	color:grey;
}




#hardButton {
	
	
	z-index:107;
	
	height:20vh;
	width:35vw;
	border:solid 1px black;
	border-radius:10vh;
	background-color:blue;
	font-size:12vh;
	font-family: 'Permanent Marker', cursive;
	box-shadow:0 10px 0 0 grey;
	outline:none;
}

#hardButton:active {
	
	
	animation:none;
	
	border:solid 1px black;
	transform:translateY(4px);
	box-shadow:0 3px 0 0 grey;
	color:grey;
}

#buttonDiv {
	position:absolute;
	left:50%;
	top:50%;
	transform:translate(-50%,-50%);
	z-index:106;
	height:60vh;
	width:50vw;
	text-align:center;
}

/*SmartPhones */
@media only screen and (max-width:600px) {
	
	
	#easyButton {
		position:absolute;
		
		z-index:106;
		
		height:20vh;
		width:50vw;
		border:solid 1px black;
		border-radius:10vh;
		background-color:blue;
		font-size:6vh;
		animation:buttonFlicker 200ms linear forwards infinite;
		box-shadow:0 10px 0 0 grey;
		outline:none;
	
	}
	
	#mediumButton {
		position:absolute;
		
		z-index:106;
		
		height:20vh;
		width:50vw;
		border:solid 1px black;
		border-radius:10vh;
		background-color:blue;
		font-size:6vh;
		animation:buttonFlicker 200ms linear forwards infinite;
		box-shadow:0 10px 0 0 grey;
		outline:none;
	}
	
	#hardButton {
		position:absolute;
		
		z-index:106;
		
		height:20vh;
		width:50vw;
		border:solid 1px black;
		border-radius:10vh;
		background-color:blue;
		font-size:6vh;
		animation:buttonFlicker 200ms linear forwards infinite;
		box-shadow:0 10px 0 0 grey;
		outline:none;
	}
}


