body{
	height:100vh;
	width:100vw;
	margin:0;
	overflow:hidden;
	position:fixed;
}

#root {
	height:100%;
	width:100%;
}

#main-canvas {
	height:100%;
	width:100%;
	background-color:black;
	z-index:0;

	
}

#gameArea {
	background-image:linear-gradient(to bottom,hsl(150,100%,67%),hsl(350,10%,80%));
	position:absolute;
	height:100%;
	width:55vh;
	left:50%;
	transform:translate(-50%,0%);
	z-index:1;
}

#jet-plane {
	height:10vh;
	width:10vh;
	position:absolute;
	z-index:104;
	user-select:none;
	background:none;
}

.arrow {
	z-index:102;
	height:10vh;
	width:10vh;
	position:absolute;
	background:none;
	user-select:none;
}


.bug {
	height:10vh;
	width:10vh;
	z-index:101;
	background-color:none;
	position:absolute;
	user-select:none;
	
}



.bullet {
	height:10vh;
	width:10vh;
	position:absolute;
	user-select:none;
	z-index:100;
}

#laserLine {
	height:8vh;
	width:115%;
	top:46vh;
	position:absolute;
	left:-7.5%;
	z-index:2;
	animation:laserShoot .2s linear forwards infinite;
	background-size:100% 100%;
	background-repeat:no-repeat;
}

@keyframes laserShoot {
	0%{background-image:url('./laser1.png')}
	11%{background-image:url('./laser2.png')}
	22%{background-image:url('./laser3.png')}
	33%{background-image:url('./laser4.png')}
	44%{background-image:url('./laser5.png')}
	55%{background-image:url('./laser6.png')}
	66%{background-image:url('./laser7.png')}
	77%{background-image:url('./laser8.png')}
	88%{background-image:url('./laser9.png')}
	95%{background-image:url('./laser10.png')}
	100%{background-image:url('./laser11.png')}
}



#robot {
	width:20vh;
	height:30vh;
	position:absolute;
	right:-21vh;
	top:38.5vh;
	user-select:none;
}



